import React, { Component } from "react";

class Input extends Component {
  initalState = {
    site: "",
    password: ""
  };
  state = this.initalState;
  render() {
    return (
      <form>
        <label>Site</label>
        <input
          type={"text"}
          name={"site"}
          value={this.state.site}
          onChange={this.handleChange}
        ></input>

        <label>Password</label>
        <input
          type={"text"}
          name={"password"}
          value={this.state.password}
          onChange={this.handleChange}
        ></input>

        <input
          type={"button"}
          onClick={this.submitForm}
          value={"Submit"}
        ></input>
      </form>
    );
  }

  handleChange = (event) => {
    const { name, value } = event.target;

    this.setState({
      [name]: value
    });
  };

  onlySpaces = (str) => {
    return /^\s*$/.test(str);
  };

  submitForm = () => {
    if (
      this.state.site === "" ||
      this.state.password === "" ||
      this.onlySpaces(this.state.site) ||
      this.onlySpaces(this.state.password)
    ) {
      console.log("noo");
      this.setState(this.initalState);
    } else {
      this.props.handleSubmit(this.state);
      this.setState(this.initalState);
    }
  };
}

export default Input;
