import React, { Component } from "react";
import App from "./App";

class Table extends Component {
  render() {
    const { characters } = this.props;
    return (
      <table>
        <this.TableHead />,
        <this.TableBody characters={characters} />
      </table>
    );
  }

  TableHead = () => {
    return (
      <thead>
        <tr>
          <th>Site</th>
          <th>Password</th>
        </tr>
      </thead>
    );
  };

  TableBody = (props) => {
    const rows = props.characters.map((row, index) => {
      return (
        localStorage.setItem("characterss" + index, row.site),
        localStorage.setItem("charactersp" + index, row.password),
        (
          <tr key={index}>
            <td>{localStorage.getItem("characterss" + index)}</td>
            <td>{localStorage.getItem("charactersp" + index)}</td>
            <td>{index}</td>
          </tr>
        )
      );
    });
    return <tbody>{rows}</tbody>;
  };
}

export default Table;
